import React, { useState } from 'react';
import { Button, FormControl, FormControlLabel, FormLabel, Radio, RadioGroup, Typography } from '@mui/material';
import './TicketShop.scss';
import {SuccessPurchase} from "./successPurchase"; // Stile importieren

export function Ticketpage() {
    const [ticketType, setTicketType] = useState('');

    const handleTicketTypeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setTicketType(event.target.value);
    };

    const handleBuyTicket = () => {
        console.log(`Kaufe ${ticketType === 'adult' ? 'Erwachsenen' : 'Kinder'}-Ticket...`);
    };

    return (
        <div className="ticket-shop">
            <Typography variant="h5" gutterBottom className="title">
                Ticket Shop
            </Typography>
            <FormControl component="fieldset" className="ticket-type">
                <FormLabel component="legend">Ticket-Typ</FormLabel>
                <RadioGroup row aria-label="ticket-type" name="ticket-type" value={ticketType} onChange={handleTicketTypeChange}>
                    <FormControlLabel value="adult" control={<Radio />} label="Erwachsener" />
                    <FormControlLabel value="child" control={<Radio />} label="Kind" />
                </RadioGroup>
                <Button variant="contained" color="primary" onClick={handleBuyTicket} className="buy-button">
                    Ticket kaufen
                </Button>
            </FormControl>
        </div>
    );
}
export default Ticketpage;