import './homepage.scss';
import Carousel from "react-material-ui-carousel";
import lionImage from '../img/lion.png';
import giraffeImage from '../img/giraffe.png';
import monkeyImage from '../img/monkey.png';
import {Alert, Button, Card, CardActions, CardContent, CardHeader, IconButton, Paper} from "@mui/material";
import Collapse from '@mui/material/Collapse';
import CloseIcon from '@mui/icons-material/Close';
import {useEffect, useState} from "react";
function Homepage() {
    const [open, setOpen] = useState(true);
    useEffect(() => {
        setOpen(true)
    }, [])
    const news = [
        {
            title: 'Lwen Baby geboren',
            text: 'Am 19.3.2023 ist der kleine Simba auf die Welt gekommen.',
            image: lionImage
        },
        {
            title: 'Affe entkommen',
            text: 'Am Donnerstag dieser Woche ist ein Affe aus dem Gehege ausgebrochen. Halten Sie ausschau!',
            image: monkeyImage
        },
        {
            title: 'Neues Streichelgehege fr die Giraffen',
            text: 'Ab dem 24.4.2023 wird ein neues Streichelgehege fr die Giraffen erffnet. Komm vorbei!',
            image: giraffeImage
        }
    ]

    return (
        <div className="homepage">
            <Collapse in={open}>
                <Alert
                    action={
                        <IconButton
                            aria-label="close"
                            color="inherit"
                            size="small"
                            onClick={() => {
                                setOpen(false);
                            }}
                        >
                            <CloseIcon fontSize="inherit" />
                        </IconButton>
                    }
                >
                    This is a School project!
                </Alert>
            </Collapse>
            <Carousel
                interval={20000}
                animation="slide"
                indicators={false}
                navButtonsAlwaysVisible={true}
                stopAutoPlayOnHover={true}
                height="100%"
                className="carousel">
                {news.map(i =>
                    <Paper className="content"
                           sx={{background: `linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(${i.image})`}}>
                        <div className="info">
                            <CardHeader title={i.title}/>
                            <CardContent>{i.text}</CardContent>
                        </div>
                    </Paper>
                )}
            </Carousel>
            <Card className="zoo-infos">
                <CardHeader title="ffnungszeiten"/>
                <CardContent>
                    <p>geffnet von:</p>
                    <p>MO: 9:00 - 18:00</p>
                    <p>DI: 9:00 - 18:00</p>
                    <p>MI: 8:00 - 18:00</p>
                    <p>DO: 9:00 - 18:00</p>
                    <p>FR: 9:00 - 18:00</p>
                    <p>SA: 9:00 - 19:00</p>
                </CardContent>
                <CardActions><Button variant="contained">Tickets kaufen</Button></CardActions>
            </Card>
        </div>
    );
}

export default Homepage;